
.block {
    background-color: rgb(62, 60, 60);
    display: flex;
    align-items: center;
    justify-content: space-around;
    
}
.wrapper {
    display: flex;
    border-radius: 5%;
    flex-direction: column;
    justify-content: space-between;
    padding: 50px 0;
}

.skillsTitle {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: "Montserrat", sans-serif;
    line-height: 1.2;
    letter-spacing: 5px;
    font-size: 36px;
    text-transform: capitalize;
    color: white;
}
.line{
    background-color: rgba(127, 255, 0, 0.73);
    width: 55px;
    height: 2px;
    margin: 10px auto;
}
.skill {
    width: 100%;
    height: 70%;
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
}

.span {
    background-color: #000000;
    color: wheat;
    
}
@media screen and (max-width: 800px) {
    .skill {
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
    }
    .containt {
        height: auto;
    }
    .works {
        height: auto;
        padding: 50px 0;
    }
}
@media screen and (max-width:350px) {
    .img {
    height: 396px;
    width: 226px;
    background-color: rgb(0, 0, 0);
    }
    .item {
            width: 100%;
    height: auto;
    top: 100px;
    left: 1px;
    }
    .item .img:before {
    content: "";
    position: absolute;
    height: 408px;
    left: 10px;
    top: -28px;
    width: 240px;
    z-index: -1;
    }
}